
import { createPortal } from "react-dom";
import Draggable from "react-draggable";
import { db } from "../database/db";
import { useState } from "react";
import { useEffect } from "react";
import { toast } from "react-toastify";
import { routes } from "../Constants";
import { projectId } from "../Constants";
import "../Css/course.css";


const initialValues = {
    name: "",
    band_height: "",
    bottom_elevation: "",
    top_elevation: "",
    band_material: "",
    select_wall: "",
    fill_material: "",
    rebar_material: "",
    rebar_overlap: "",
    total_per_each: "",
    rebar_quantity: "",
    sq_grouted_per_cy: "",
    detail: "",
    total_course: "0",
    total_material_unit: "",
    total_rebar_length: "",
    total_rebar_lf: "0",
    rebar_lf_per_ton: "0",
    area_grouted_sq: "",
    total_grout_cy: "",
    total_area_grout_sq: "",
    total_rebar_length: "",
    total_material_units: "",
    total_courses: "",
    total_rebar_linear_feet: 0,
    rebar_lf_ton: "",
    sq_ft_filled_grouted: "",
    deducted_area_vertically: "",
    total_sq_area_filled: "",
    total_sq_fill_materials: "",
}
export default function CoursePopup({ pop, setDisplay, materials, wallHeight, wall_length, total_spaces_filled }) {
    // console.log(materials)
    const walllength = wall_length;
    const totalSpacesFilled = total_spaces_filled;
    const [wallMaterials, setWallMaterials] = useState([]);
    const [formData, setFormData] = useState({ ...initialValues })
    const handleCreate = async (e) => {
        e.preventDefault()
        //console.log("formData",formData)
        await db.course_band.put(formData)
        setFormData(initialValues)
        setDisplay(false);
        toast.success('Band Saved Successfully!', { ...toastOptions });
    }
    const toastOptions = {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: 0,
    };
    const handleInputChange = (event) => {
        const { name, value } = event.target;
         setFormData({
            ...formData,
            [name]: value,
          });
          //calcular_todo();
    };



    const calcular_todo=()=>{

        calcular_total_rebar_linear_feet();
        calcular_rebar_lf_ton();

    }











    const calcular_total_material_units = () => {
        const area_material = parseFloat((formData.band_material.length * formData.band_material.height) / 144).toFixed(3);

        const total_material_units = ((walllength * formData.band_height) / area_material).toFixed(0);
        console.log("band_height", formData.band_height);
        console.log("walllength", walllength);
        console.log("area_material", area_material);
        console.log("total_material_units", total_material_units);
        return total_material_units;
    }


    const calcular_total_courses = () => {
        const band_material_height_feet = parseFloat(formData.band_material.height / 12).toFixed(3);
        const total_courses = (formData.band_height / band_material_height_feet).toFixed(0);

        console.log("band_height", formData.band_height);
        console.log("band_material_height_feet", band_material_height_feet);
        console.log("total_courses", total_courses);
        return total_courses;
    }


    const calcular_total_rebar_length = () => {

        const total_per_each = ((parseFloat(formData.rebar_overlap) || 0) +
            (parseFloat(formData.band_material.length / 12) || 0) ||
            0
        ).toFixed(3);

        const total_rebar_length =

            ((walllength / parseFloat(formData.band_material.length / 12)) * total_per_each)
                .toFixed(3)
            ;
        return total_rebar_length;
    }




    const calcular_total_rebar_linear_feet = () => {

        const total_ = (parseFloat(formData.total_rebar_length) * parseFloat(formData.rebar_quantity) * parseFloat(formData.total_courses)).toFixed(3);

        console.log("total_rebar_length", formData.total_rebar_length);
        console.log("rebar_quantity", formData.rebar_quantity);
        console.log("formData.total_courses", formData.total_courses);
        console.log("total_rebar_linear_feet", total_);
        
        return total_;
    }
    const calcular_rebar_lf_ton = () => {
        
        const total_ = parseFloat(formData.total_rebar_linear_feet/formData.fill_material.length).toFixed(6);       
        console.log("formData.total_rebar_linear_feet", formData.total_rebar_linear_feet);
        console.log("formData.fill_material.length", formData.fill_material.length);
        return total_;
    }
    const calcular_sq_ft_filled_grouted = () => {

        const total_ = parseFloat(formData.band_height*walllength).toFixed(3); 

        return total_;
    }
    const calcular_deducted_area_vertically = () => {

        const total_ = parseFloat((formData.band_height*totalSpacesFilled)*0.66).toFixed(3);  
        console.log("formData.band_height", formData.band_height);
        console.log("totalSpacesFilled", totalSpacesFilled);

        return total_;
    }
    const calcular_total_sq_area_filled = () => {

        const total_ = parseFloat((formData.sq_ft_filled_grouted-formData.deducted_area_vertically)).toFixed(2);  

        return total_;
    }
    const calcular_total_sq_fill_materials = () => {

        const total_ = parseFloat((formData.total_sq_area_filled/formData.sq_grouted_per_cy)).toFixed(2); 

        return total_;
    }

















    useEffect(() => {
        const fetchWallMaterials = async () => {
            try {
                const response = await fetch('/api/materials/' + projectId, {
                    method: 'GET',
                    headers: {
                        Authorization: `Bearer ${routes.__token}`,
                    },
                });

                if (!response.ok) {
                    // Handle error if the response is not OK (status code 2xx)
                    // Example: console.log('Error fetching wall materials data');
                    return;
                }

                const data = await response.json();
                setWallMaterials(data);
            } catch (error) {
                // Handle error if there's an issue with the API call
                // Example: console.error('API call failed:', error);
            }
        };
        fetchWallMaterials();
    }, []);
    // console.log(materials)
    useEffect(() => {
        setFormData((prevFormData) => ({
            ...prevFormData,
            top_elevation: (
                (parseFloat(formData.band_height) || 0) +
                (parseFloat(formData.bottom_elevation) || 0) ||
                0
            ).toFixed(3),
        }))
    }, [
        formData.band_height,
        formData.bottom_elevation
    ])
    // console.log('check band material height', formData.band_material.length);
    useEffect(() => {
        setFormData((prevFormData) => ({
            ...prevFormData,
            total_per_each: (
                (parseFloat(formData.rebar_overlap) || 0) +
                (parseFloat(formData.rebar_material.length / 12) || 0) ||
                0
            ).toFixed(3)

        })
        );
        // calcular_total_rebar_length();
    }, [
        formData.rebar_overlap,
        formData.band_material.length
    ]);

    useEffect(() => {
        setFormData((prevFormData) => ({
            ...prevFormData,
            total_rebar_length: calcular_total_rebar_length()
            ,
        }))
    }, [
        formData.rebar_overlap,
        formData.band_material.length
    ]);

    useEffect(() => {
        setFormData((prevFormData) => ({
            ...prevFormData,
            total_courses: calcular_total_courses()
            ,
        }))
    }, [
        formData.rebar_overlap,
        formData.band_material.length
    ])
    useEffect(() => {
        setFormData((prevFormData) => ({
            ...prevFormData,
            total_material_units: calcular_total_material_units()
            ,
        }))
    }, [
        formData.rebar_overlap,
        formData.band_material.length
    ])



    useEffect(() => {
        setFormData((prevFormData) => ({
            ...prevFormData,
            total_rebar_linear_feet: calcular_total_rebar_linear_feet()
            ,
        }))
    }, [
        formData.rebar_quantity,
        formData.fill_material,
    ])

    useEffect(() => {
        setFormData((prevFormData) => ({
            ...prevFormData,
            rebar_lf_ton: calcular_rebar_lf_ton()
            ,
        }))
    }, [
        formData.total_rebar_linear_feet,
        formData.band_material.length
    ])

    useEffect(() => {
        setFormData((prevFormData) => ({
            ...prevFormData,
            sq_ft_filled_grouted: calcular_sq_ft_filled_grouted()
            ,
        }))
    }, [
        formData.band_height,
        formData.band_material.length
    ])

    useEffect(() => {
        setFormData((prevFormData) => ({
            ...prevFormData,
            deducted_area_vertically: calcular_deducted_area_vertically()
            ,
        }))
    }, [
        formData.band_height,
        formData.band_material.length
    ])

    useEffect(() => {
        setFormData((prevFormData) => ({
            ...prevFormData,
            total_sq_area_filled: calcular_total_sq_area_filled()
            ,
        }))
    }, [
        formData.deducted_area_vertically,
        formData.band_material.length
    ])

    useEffect(() => {
        setFormData((prevFormData) => ({
            ...prevFormData,
            total_sq_fill_materials: calcular_total_sq_fill_materials()
            ,
        }))
    }, [
        formData.sq_grouted_per_cy,
        formData.band_material.length
    ])

    return (
        createPortal(
            <Draggable>
                <div className="modal" style={{ display: pop ? 'unset' : 'none' }}>
                    <div className="modal-header">
                        Create Course
                        <span className="close-btn" onClick={() => setDisplay(false)}>&times;</span>
                    </div>
                    <form className="modal-body" onSubmit={handleCreate}>
                        <div className="course-input">
                            <label className="label">Name</label>
                            <input
                                className="border-blue"
                                type="text"
                                name="name"
                                value={formData.name}
                                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">
                                Band Material
                            </label>
                            <select
                                className="border-blue"
                                onChange={(e) => setFormData({ ...formData, band_material: wallMaterials[e.target.value] })}
                                name="band_material">
                                <option value="" hidden>-- Select --</option>
                                {wallMaterials?.map((item, index) => {
                                    return (
                                        <option key={index} value={index}>{item.name}</option>
                                    )
                                })}
                            </select>
                        </div>
                        <div className="course-input">
                            <label className="label">Band Height</label>
                            <input
                                className="border-blue"
                                type="number"
                                value={formData.band_height}
                                onChange={(e) => setFormData({ ...formData, band_height: e.target.value })}
                                name="band_height" />
                        </div>
                        <div className="course-input">
                            <label className="label">Total Courses</label>
                            <input
                                className="border-blue"
                                type="number"
                                value={formData.total_courses}
                                onChange={(e) => setFormData({ ...formData, total_courses: e.target.value })}
                                name="total_courses" />
                        </div>
                        <div className="course-input">
                            <label className="label">Bottom Elevation</label>
                            <input
                                className="border-blue"
                                type="number"
                                name="bottom_elevation"
                                value={formData.bottom_elevation}
                                onChange={(e) => setFormData({ ...formData, bottom_elevation: e.target.value })}
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">Top Elevation</label>
                            <input
                                type="number"
                                name="top_elevation"
                                value={formData.top_elevation}
                                onChange={(e) => setFormData({ ...formData, top_elevation: e.target.value })}
                                readOnly
                            />
                        </div>
                        
                        <div className="course-input">
                            <label className="label">total materials units</label>
                            <input
                                type="number"
                                name="total_material_units"
                                value={formData.total_material_units}
                                onChange={(e) => setFormData({ ...formData, total_material_units: e.target.value })}
                                readOnly
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">Fill Material</label>
                            <select
                                className="border-blue"
                                 onChange={(e) => setFormData({ ...formData, fill_material: wallMaterials[e.target.value] })}
                                //onChange={calcular_rebar_lf_ton}
                                name="fill_material">
                                <option value="" hidden>-- Select --</option>
                                {wallMaterials?.map((item, index) => {
                                    return (
                                        <option key={index} value={index}>{item.name}</option>
                                    )
                                })}
                            </select>
                        </div>
                        <div className="course-input">
                            <label
                                className="label"
                            >
                                Rebar Material
                            </label>
                            <select
                                className="border-blue"
                                onChange={(e) => setFormData({ ...formData, rebar_material: wallMaterials[e.target.value] })}
                                name="rebar_material">
                                <option value="" hidden>-- Select --</option>
                                {wallMaterials?.map((item, index) => {
                                    return (
                                        <option key={index} value={index}>{item.name}</option>
                                    )
                                })}
                            </select>
                        </div>
                        <div className="course-input">
                            <label className="label">Rebar Overlap</label>
                            <input
                                className="border-blue"
                                type="number"
                                value={formData.rebar_overlap}
                                onChange={(e) => setFormData({ ...formData, rebar_overlap: e.target.value })}
                                name="rebar_overlap" />
                        </div>
                        <div className="form-row"></div>
                        <div className="course-input">
                            <label className="label">Total per each</label>
                            <input
                                className="border-blue"
                                type="number"
                                name="total_per_each"
                                value={formData.total_per_each}
                                onChange={calcular_total_rebar_length}

                            />
                        </div>
                        <div className="course-input">
                            <label className="label">total rebar length</label>
                            <input

                                type="number"
                                name="total_rebar_length"
                                value={formData.total_rebar_length}

                                readOnly
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">Rebar Quantity</label>
                            <input
                                className="border-blue"
                                type="number"
                                value={formData.rebar_quantity}
                                onChange={(e) => setFormData({ ...formData, rebar_quantity: e.target.value })}
                               
                                
                                name="rebar_quantity"
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">Total rebar linear feet</label>
                            <input
                               
                                type="number"
                                value={formData.total_rebar_linear_feet}
                               // onChange={(e) => setFormData({ ...formData, total_rebar_linear_feet: e.target.value })}
                                name="total_rebar_linear_feet"
                                
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">Rebar lf/ton</label>
                            <input
                                
                                type="number"
                                value={formData.rebar_lf_ton}
                                //onChange={(e) => setFormData({ ...formData, rebar_lf_ton: e.target.value })}
                                name="rebar_lf_ton"
                                 
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">Sq Grouted per cy </label>
                            <input
                                className="border-blue"
                                name="sq_grouted_per_cy"
                                type={'number'}
                                value={formData.sq_grouted_per_cy}
                                onChange={(e) => setFormData({ ...formData, sq_grouted_per_cy: e.target.value })}
                            />
                        </div>

                        <div className="course-input">
                            <label className="label">Sq ft area fille/Grouted</label>
                            <input

                                type="number"
                                name="sq_ft_filled_grouted"
                                value={formData.sq_ft_filled_grouted}

                                readOnly
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">deduct area vertically filled in wall</label>
                            <input

                                type="number"
                                name="deducted_area_vertically"
                                value={formData.deducted_area_vertically}

                                readOnly
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">total sq area filled</label>
                            <input

                                type="number"
                                name="total_sq_area_filled"
                                value={formData.total_sq_area_filled}

                                readOnly
                            />
                        </div>
                        <div className="course-input">
                            <label className="label">total fill materials</label>
                            <input

                                type="number"
                                name="total_sq_fill_materials"
                                value={formData.total_sq_fill_materials}

                                readOnly
                            />
                        </div>



                        <div className="course-input">
                            <label className="label">
                                Select Wall
                            </label>
                            <select
                                className="border-blue"
                                onChange={(e) => setFormData({ ...formData, select_wall: e.target.value })}
                                name="select_wall">
                                <option value="" hidden>-- Select --</option>
                                <option value="main">Main</option>
                                {/* <option value="top">Top</option>
                                <option value="bottom">Bottom</option>
                                <option value="top_botton">Top Bottom</option> */}
                            </select>
                        </div>
                        <div className="course-input-textarea">
                            <label className="label">Details</label>
                            <textarea
                                name="detail"
                                rows={5}
                                onChange={(e) => setFormData({ ...formData, detail: e.target.value })}
                            />
                        </div>
                        <button type="button" className="btn btn-success mt-2" onClick={calcular_todo}>Calculate</button>
                        <button type="submit" className="btn btn-primary mt-2">Create</button>
                    </form>
                </div>
            </Draggable>,
            document.querySelector('#root')
        )
    )
}